import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import Home from './pages/Home';

import Contact from './pages/Contact';

import Knowledges from './pages/Knowledges';

import NotFound from './pages/NotFound';

import Portfolio from './pages/Portfolio';

import Formations from './pages/Formations';

const App = () => {
  return (
    <>
    <BrowserRouter>
        <Switch>
          <Route path = "/" exact component={Home} />
          <Route path="/portfolio" exact component={() => <Home isFirst={true} />}/>
          <Route path = "/portfolio/formations" component={Formations} />
          <Route path = "/portfolio/competences" component={Knowledges} />
          <Route path = "/portfolio/contact" component={Contact} />
          <Route path = "/portfolio/projet" component={Portfolio} />
          <Route component={NotFound} />
        </Switch>
    </BrowserRouter>

    </>    
  );
};

export default App;