import React from 'react';
import Formation from '../components/Formation';
import Navigation from '../components/Navigation';

import { formationData } from '../data/formationData';

const Formations = () => {
    let { formationScolaire, formationPerso,icons } = formationData;
    return (
        <div className="formations">
            <Navigation/>
            <div className="formationsContent">
                <div className="formationsScolaire">
                    <h3>Formations scolaires</h3>
                    <div className="cursus">
                        {formationScolaire.map(formation =>
                            <Formation formation={formation} key={formation.id}/>
                        )}
                    </div>
                    

                </div>
                <div className="formationsPerso">
                    <h3>Auto formations</h3>
                    <div className="message">
                        {<p>{formationPerso.message}</p>}
                        <p>Ces technologies et outils sont entre autres :</p>
                    </div>
                    <div className="list-icons">
                        {icons.map((line,key) =>
                            <div className="line-icons" key={key}>
                                {line.map(
                                    (iconPath, key) => 
                                        <div className="icon" key={key}>
                                            <img src={iconPath}
                                                alt="L'icon d'un langage"></img>
                                        </div>
                                )

                                }
                            </div>
                        )

                        }
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Formations;
