import React, { Component } from 'react';
import ProgressBar from './ProgressBar';

class Languages extends Component {
    state = {
        languages: [
            {id: 1, value: "Javascript", xp:2},
            {id: 2, value: "HTML & Css", xp:3},
            {id: 3, value: "Php", xp:3},
            {id: 4, value: "Java", xp:3},
            {id: 5, value: "Bash", xp:3},
            {id: 6, value: "Python", xp:2},
            {id: 7, value: "SQL", xp:2}
        ],
        frameworks: [
            {id: 1, value: "Symfony", xp:3},
            {id: 2, value: "Bootstrap", xp:2},
            {id: 3, value: "React", xp:1},
            {id: 4, value: "Laravel", xp:1.5},
            {id: 5, value: "C#", xp:2}
        ]
    }
    render() {
        let {languages, frameworks} = this.state;
        return (
            <div className="languagesFrameworks">
               <ProgressBar
                    languages={languages}
                    className="languagesDisplay"
                    title="languages"
               />
               <ProgressBar
                    languages={frameworks}
                    title="frameworks & bibliothèques"
                    className="frameworksDisplay"
               />            
            </div>
        );
    }
}

export default Languages;