import React from 'react';
import {NavLink} from 'react-router-dom'; 

var state = true;

const Navigation = (props) => {
   //let src = (props.isFirst && state) ? "media/baobab.jpg" : "portfolio/media/baobab.jpg";
   let src = props.isFirst ? "media/baobab.jpg" : "./media/baobab.jpg";
    src = state ? src : ( props.isFirst ? "portfolio/" + src : src);
    state = false;
    return (
       <div className="sidebar">
        <div className="id">
         < div className="idContent">
             <img src={src} alt="profil-pic"/>
             <h3> Binnet BARRY</h3>
          </div>
        </div>
        <div className="Navigation">
            <ul>
                <li>
                <NavLink exact to="/portfolio" activeClassName="navActive">
                   <i className="fas fa-home"></i> 
                   <span> Accueil </span>
                </NavLink>
                </li>
                <li>
                <NavLink exact to="/portfolio/formations" activeClassName="navActive">
                   <i className="fas fa-building"></i> 
                   <span> Formations </span>
                </NavLink>
                </li>
                <li>
                <NavLink exact to="/portfolio/competences" activeClassName="navActive">
                   <i className="fas fa-mountain"></i> 
                   <span> Compétences </span>
                </NavLink>
                </li>
                <li>
                <NavLink exact to="/portfolio/projet" activeClassName="navActive">
                   <i className="fas fa-images"></i> 
                   <span> Portfolio </span>
                </NavLink>
                </li>
                <li>
                <NavLink exact to="/portfolio/contact" activeClassName="navActive">
                   <i className="fas fa-address-book"></i> 
                   <span> Contact </span>
                </NavLink>
                </li>
            </ul> 
        </div>
        <div className="socialNetwork">
           <ul>
              <li>
                 <a href="https://www.google.com" target="_blank" rel="noopener noreferrer">
                    <i className="fab fa-linkedin"></i>
                  </a>
              </li>
              <li>
                 <a href="https://www.google.com" target="_blank" rel="noopener noreferrer">
                    <i className="fab fa-gitlab"></i>
                  </a>
              </li>
              <li>
                 <a href="https://www.google.com" target="_blank" rel="noopener noreferrer">
                    <i className="fab fa-facebook"></i>
                  </a>
              </li>
           </ul>
           <div className="signature">
              <p> Copyright - 2022</p>
           </div>
        </div>
       </div>

    );
};

export default Navigation;