import React from 'react';
import Navigation from '../components/Navigation';
import { CopyToClipboard } from 'react-copy-to-clipboard';

const Contact = () => {
    return (
        <div className="contact">
            <Navigation />
            <div className="contactContent">
                <div className="header"> </div>
                    <div className="contactBox">
                        <h1> Contactez-moi </h1>
                        <ul>
                            <li>
                                <i className="fas fa-map-marker-alt"></i>
                                <span> Amiens</span>
                            </li>
                            <li>
                                <i className="fas fa-mobile-alt"> </i> 
                                <CopyToClipboard text="+33 7 68 80 44 66">
                                    <span className="clickInput" 
                                        onClick={()=> {alert ('Téléphone copié !}');}}>+33 7 68 80 44 66
                                    </span>
                                </CopyToClipboard>
                            </li>
                            <li>
                                <i className="far fa-envelope"></i>
                                <CopyToClipboard text="mamadoubinnetbarry@yahoo.fr">
                                    <span className="clickInput" 
                                         onClick={()=> {alert ('E-mail copié !');}}>mamadoubinnetbarry@yahoo.fr
                                    </span>
                                </CopyToClipboard>
                            </li>
                        </ul>
                    </div>
                    <div className="socialNetwork">
                            <ul>
                                <a href="https://www.google.com" target="_blank" rel="noopener noreferrer">
                                    <h4>LinkedIn</h4>
                                    <i className="fab fa-linkedin"></i>
                                </a>
                                <a href="https://www.google.com" target="_blank" rel="noopener noreferrer">
                                    <h4>Gitlab</h4>
                                    <i className="fab fa-gitlab"></i>
                                </a>
                                <a href="https://www.google.com" target="_blank" rel="noopener noreferrer">
                                    <h4>facebook</h4>
                                    <i className="fab fa-facebook"></i>
                                </a>
                            </ul>
                    </div>              
            </div>
        </div>
    );
};

export default Contact;