import React, { Component } from 'react';

class Formation extends Component {

    render() {
        let {formation} = this.props;
        return (
            <div className="formation">
                <h4>
                    <i className="fas fa-graduation-cap"></i>
                    <span>{formation.diploma} parcours {formation.cursus}</span>
                </h4>
                <ul>
                    <li>
                        <i className="fas fa-university"></i>
                        <a className='extern-link'
                           href={formation.etablissement_link}>
                            <span>{formation.etablissement}</span>
                        </a>
                    </li>
                    <li>
                        <i className="fas fa-map-marker-alt"></i>
                        <span>{formation.country}</span>
                    </li>
                    <li>
                        <i className="fas fa-calendar-alt"></i>
                        {formation.endYear 
                            ? <span>De { formation.startYear + " à " + formation.endYear}</span> 
                            : <span>Depuis {formation.startYear } </span>
                        }
                    </li>  
                </ul>
        </div>
        );
    }
}

export default Formation;